import { Component, OnInit } from "@angular/core";

import { EnquiriesService } from "../services/enquiries.service";
import { ActivatedRoute, Router } from "@angular/router";
import swal from "sweetalert2";
import { Enquiries } from "app/shared/models/enquiries.model";

@Component({
  selector: "app-edit-enquiries",
  templateUrl: "./edit-enquiries.component.html",
  styleUrls: ["./edit-enquiries.component.scss"]
})
export class EditEnquiriesComponent implements OnInit {
  updatedEnquiries;
  enquiriesid;

  constructor(
    public enquiriesservice: EnquiriesService,
    public router: Router,
    public activeRouter: ActivatedRoute
  ) {
    this.updatedEnquiries = new Enquiries({});
    this.enquiriesid = this.activeRouter.snapshot.params["id"];
  }

  ngOnInit() {
    this.enquiriesservice.getEnquiriesById(this.enquiriesid).subscribe(
      (data: any) => {
        console.log("data", data);
        this.updatedEnquiries = data;
      },
      error => {
        console.log("error-->", error);
      }
    );
  }

  edit() {
    swal
      .fire({
        title: "Do you want to update the Enquiries?",
        text: "You won't be able to revert it back",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Update"
      })
      .then(result => {
        if (result.value) {
          this.enquiriesservice
            .editEnquiries(this.updatedEnquiries, this.enquiriesid)
            .subscribe(
              (data: any) => {
                console.log("enquiries added successfully");
                swal
                  .fire("Updated!", "Enquiries Updated.", "success")
                  .then(() => {
                    this.router.navigate(["enquiries"]);
                  });
                this.ngOnInit();
              },
              error => {
                swal.fire("Not Updated!", "There was some error.", "error");
                console.log("Error", error);
              }
            );
        }
      });

    console.log("Submitted>>>>>>>>>", this.updatedEnquiries);
  }
}
