import { Component, OnInit } from "@angular/core";
import { EnquiriesService } from "../services/enquiries.service";
import { Router } from "@angular/router";
import { Enquiries } from "../../../shared/models/enquiries.model";

import swal from "sweetalert2";

@Component({
  selector: "app-add-enquiries",
  templateUrl: "./add-enquiries.component.html",
  styleUrls: ["./add-enquiries.component.scss"]
})
export class AddEnquiriesComponent implements OnInit {
  newenquiries: Enquiries;
  submitting: boolean;

  constructor(
    public enquiriessservice: EnquiriesService,
    public router: Router
  ) {
    this.newenquiries = new Enquiries({});
  }

  ngOnInit() {}

  submit() {
    console.log("Submitted>>>>>>>>>", this.newenquiries);

    swal
      .fire({
        title: "Do you want to add the enquiries ?",
        text: "New enquiries will be added in Enquiries List",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Add"
      })
      .then(result => {
        if (result.value) {
          //dev

          this.enquiriessservice.addEnquiries(this.newenquiries).subscribe(
            (data: any) => {
              swal.fire("Add!", "Enquiries has been added.", "success");
              //dev
              console.log("Dataaa changeddd", data);
            },
            error => {
              swal.fire("Not Added!", "There was some error.", "error");
              //dev
              console.log("ERRORR", error);
            }
          );
        }
      });
  }
}
