import { NgModule } from "@angular/core";
import { Routes, RouterModule } from "@angular/router";
import { EnquiriesComponent } from "./enquiries.component";
import { AddEnquiriesComponent } from "./add-enquiries/add-enquiries.component";
import { EditEnquiriesComponent } from "./edit-enquiries/edit-enquiries.component";

const routes: Routes = [
  {
    path: "",
    component: EnquiriesComponent,
    data: {
      title: "Enquiries"
    }
  },

  {
    path: "add",
    component: AddEnquiriesComponent,
    data: {
      title: "Add Enquiries"
    }
  },

  {
    path: "edit/:id",
    component: EditEnquiriesComponent,
    data: {
      title: "Edit Enquiries"
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class EnquiriesRoutingModule {}
