import { Injectable } from "@angular/core";
import { BaseService } from "../../../shared/services/base.service";
import { HttpClient } from "@angular/common/http";
import { Enquiries } from "app/shared/models/enquiries.model";

@Injectable()
export class EnquiriesService extends BaseService {
  constructor(public http: HttpClient) {
    super("enquiries");
  }

  getEnquiries() {
    return this.http.get(this.url);
  }
  addEnquiries(data) {
    return this.http.post(this.url + "add/", data, this.getOptions());
  }

  getEnquiriesById(id) {
    return this.http.get(this.url + "edit/" + id, this.getOptions());
  }
  editEnquiries(updatedData: Enquiries, id) {
    return this.http.put(
      this.url + "edit/" + id,
      updatedData,
      this.getOptions()
    );
  }
  deleteEnquiries(id) {
    return this.http.delete(this.url + "delete/" + id, this.getOptions());
  }
}
