import { Component, OnInit } from "@angular/core";
import { Router } from "@angular/router";
import { EnquiriesService } from "./services/enquiries.service";
import swal from "sweetalert2";

@Component({
  selector: "app-enquiries",
  templateUrl: "./enquiries.component.html",
  styleUrls: ["./enquiries.component.scss"]
})
export class EnquiriesComponent implements OnInit {
  enquiriesList;

  constructor(
    public enquiriesservice: EnquiriesService,
    private router: Router
  ) {
    this.enquiriesList = [];
  }

  ngOnInit() {
    this.enquiriesservice.getEnquiries().subscribe(
      (data: any) => {
        this.enquiriesList = data;
        console.log(data);
      },
      error => {
        console.log(error);
      }
    );
  }

  delete(id) {
    swal
      .fire({
        title: "Do you want to delete the Applicant?",
        text: "You won't be able to revert it back",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
        confirmButtonText: "Delete"
      })
      .then(result => {
        if (result.value) {
          this.enquiriesservice.deleteEnquiries(id).subscribe(
            (data: any) => {
              console.log("Deleted successfully");
              swal.fire("Deleted!", "Applicant Deleted.", "success");
              this.router.navigate(["/enquiries"]);
              this.ngOnInit();
            },
            error => {
              swal.fire("Not Deleted!", "There was some error.", "error");
              console.log("Error", error);
            }
          );
        }
      });
  }
}
